


















































































































































































































import uniRate from "@/components/uni-rate/uni-rate.vue"
export default {
	components: {uniRate},
	data() {
		return {
			//控制渐变标题栏的参数
			beforeHeaderzIndex: 11,//层级
			afterHeaderzIndex: 10,//层级
			beforeHeaderOpacity: 1,//不透明度
			afterHeaderOpacity: 0,//不透明度
			//是否显示返回按钮



			//轮播主图数据
			swiperList: [],
			//轮播图下标
			currentSwiper: 0,
			anchorlist:[],//导航条锚点
			selectAnchor:0,//选中锚点
			serviceClass: '',//服务弹窗css类，控制开关动画
			specClass: '',//规格弹窗css类，控制开关动画
			shareClass:'',//分享弹窗css类，控制开关动画
			// 商品信息
			storeId:"",
			goodsData:{},
			isAssemble:false,// 是否拼团
			pintuanData:{},
			commentData:{},//评论列表
			degreeData:"",//好评度
			selectSpec:null,//选中规格
			//商品描述html
			descriptionStr:'<div style="text-align:center;"></div>'
		};
	},
	onReady(){
		this.calcAnchor();//计算锚点高度，页面数据是ajax加载时，请把此行放在数据渲染完成事件中执行以保证高度计算正确
	},
	onLoad(option) {
		let _this = this;

		//小程序隐藏返回按钮
		_this.showBack = false;

		//option为object类型，会序列化上个页面传递的参数
		let id=option.id;
		// 商品基本信息
		_this.mPost("/server/p/product",{
			id:id,
		}).then(res=>{
			if(res.code=='1'){
				if(res.data){
					let data=res.data;
					_this.goodsData=data;
					_this.descriptionStr=data.describes;
					_this.storeId = data.storeId;
					// 轮播图
					if(data.pictures){
						_this.swiperList=[...data.pictures];
					}
					//是否可拼团
					_this.isAssemble=(data.isAssemble==1);
					if(_this.isAssemble){
						_this.getPintuanUsers(id);
					}
				}
			}
		})
		// 商品评论列表
		_this.getComments(id);
		//好评度
		_this.getDegree(id);
		// this.calcAnchor();//计算锚点高度，页面数据是ajax加载时，请把此行放在数据渲染完成事件中执行以保证高度计算正确
	},
	onPageScroll(e) {
		//锚点切换
		this.selectAnchor = e.scrollTop>=this.anchorlist[2].top?2:e.scrollTop>=this.anchorlist[1].top?1:0;
		//导航栏渐变
		let tmpY = 375;
		e.scrollTop = e.scrollTop > tmpY ? 375 : e.scrollTop;
		this.afterHeaderOpacity = e.scrollTop * (1 / tmpY);
		this.beforeHeaderOpacity = 1 - this.afterHeaderOpacity;
		//切换层级
		this.beforeHeaderzIndex = e.scrollTop > 0 ? 10 : 11;
		this.afterHeaderzIndex = e.scrollTop > 0 ? 11 : 10;
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		uni.showToast({ title: '触发上拉加载' });
	},
	methods: {
		goHome(){
			uni.switchTab({  
                url: '/pages/tabBar/home'  
            });  
		},
		//进店
		goStore(){
			console.log('进店');
			uni.navigateTo({
				url:"/pages/store/store?storeid="+this.storeId
			})
		},
		// 拼团用户列表
		getPintuanUsers(id){
			let _this = this;
			this.mPost("/server/g/group/buy/users",{
				start:0,
				length:100,
				productId:id
			}).then(res=>{
				console.log(res);
				if(res.code=='1'){
					if(res.data){
						_this.pintuanData=res.data.list;
					}
				}
			})
		},
		//评论列表
		getComments(id){
			let _this = this;
			this.mPost("/server/c/comments",{
				start:0,
				length:100,
				productId:id
			}).then(res=>{
				if(res.code=='1'){
					if(res.data){
						_this.commentData=res.data.list;
					}
				}
			})
		},
		//好评度
		getDegree(id){
			let _this = this;
			this.mPost("/server/c/praise/degree",{
				productId:id
			}).then(res=>{
				if(res.code=='1'){
					if(res.data){
						_this.degreeData=res.data;
					}
				}
			})
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
// 		//消息列表
// 		toMsg(){
// 			uni.navigateTo({
// 				url:'../msg/msg'
// 			})
// 		},
// 		// 客服
// 		toChat(){
// 			uni.navigateTo({
// 				url:"../msg/chat/chat?name=客服008"
// 			})
// 		},
		// 分享
		share(){
			this.shareClass = 'show';
		},
		hideShare(){
			this.shareClass = 'hide';
			setTimeout(() => {
				this.shareClass = 'none';
			}, 150);
		},
		//拼团
		pintuan(){
			let type=2;
			let carNum=1;
			uni.navigateTo({
				url:"/pages/order/pay?storeid="+this.storeid+"&totalCount="+carNum+"&type="+type
			})
		},
		// 加入购物车
		joinCart(){
			if(this.selectSpec==null){
				return this.showSpec(()=>{
					uni.showToast({title: "已加入购物车"});
				});
			}
			uni.showToast({title: "已加入购物车"});
		},
		//立即购买
		buy(){
			if(this.selectSpec==null){
				return this.showSpec(()=>{
					this.toConfirmation();
				});
			}
			this.toConfirmation();
		},
		//跳转确认订单页面
		toConfirmation(){
			let tmpList=[];
			let goods = {id:this.goodsData.id,img:'../../static/img/goods/p1.jpg',name:this.goodsData.name,spec:'规格:'+this.goodsData.spec[this.selectSpec],price:this.goodsData.price,number:this.goodsData.number};
			tmpList.push(goods);
			uni.setStorage({
				key:'buylist',
				data:tmpList,
				success: () => {
					uni.navigateTo({
						url:'../order/confirmation'
					})
				}
			})
		},
		//跳转评论列表
		showComments(goodsid){
			
		},
		//选择规格
		setSelectSpec(index){
			this.selectSpec = index;
		},
		//减少数量
		sub(){
			if(this.goodsData.number<=1){
				return;
			}
			this.goodsData.number--;
		},
		//增加数量
		add(){
			this.goodsData.number++;
		},
		//跳转锚点
		toAnchor(index){
			this.selectAnchor = index;
			uni.pageScrollTo({scrollTop: this.anchorlist[index].top,duration: 200});
		},
		//计算锚点高度
		calcAnchor(){
			this.anchorlist=[
				{name:'主图',top:0},
				{name:'评价',top:0},
				{name:'详情',top:0}
			]
			let commentsView = uni.createSelectorQuery().select("#comments");
			commentsView.boundingClientRect((data) => {
				let statusbarHeight = 0;
				//APP内还要计算状态栏高度



				let headerHeight = uni.upx2px(100);
				this.anchorlist[1].top = data.top - headerHeight - statusbarHeight;
				this.anchorlist[2].top = data.bottom - headerHeight - statusbarHeight;
				
			}).exec();
		},
		//返回上一页
		back() {
			uni.navigateBack();
		},
		//服务弹窗
		showService() {
			console.log('show');
			this.serviceClass = 'show';
		},
		//关闭服务弹窗
		hideService() {
			this.serviceClass = 'hide';
			setTimeout(() => {
				this.serviceClass = 'none';
			}, 200);
		},
		//规格弹窗
		showSpec(fun) {
			console.log('show');
			this.specClass = 'show';
			this.specCallback = fun;
		},
		specCallback(){
			return;
		},
		//关闭规格弹窗
		hideSpec() {
			this.specClass = 'hide';
			//回调

			this.selectSpec&&this.specCallback&&this.specCallback();
			this.specCallback = false;
			setTimeout(() => {
				this.specClass = 'none';
			}, 200);
		},
		discard() {
			//丢弃
		}
	},
	mounted () {
		
	}
};
